<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Alert </h3>
    <button @click="alert()" class="button">Default Alert</button>
    <br>
    <p>Script</p>
    <pre v-prettify class="prettyprint linenums"><code v-pre>this.$alert({
        caption: 'Alert Title',   // Title
        content: 'Alert Content',       // 
        type: 'Alert Type',      // default()info, success, alert, warning
        cb: () => {}      // 
    })</code></pre>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    methods: {
      alert() {
        const component = this
        component.$alert({
          caption: '',
          content: '',
          cb() {
            component.$message({
              caption: '',
              content: '',
              type: 'info'
            })
          }
        })
      }
    }
  }
</script>
<style lang="scss">
  
</style>



