<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Tabs </h3>
    <div class="box">
      <tabs :list="list" :value.sync="value">
        <div slot="div1" class="padding15 bg-grayLighter">div1</div>
        <div slot="div2" class="padding15 bg-grayLighter">div2</div>
      </tabs>
      <br>
      <p class="fg-gray">Props </p>
      <table class="table striped hovered bordered border">
        <thead>
          <th>Prop</th>
          <th>Type</th>
          <th>Required</th>
          <th>Default</th>
          <th>Description</th>
        </thead>
        <tbody>
          <tr>
            <td>list</td>
            <td>Array</td>
            <td>True</td>
            <td></td>
            <td>Title</td>
          </tr>
          <tr>
            <td>value</td>
            <td>String</td>
            <td>True</td>
            <td></td>
            <td>Panel()</td>
          </tr>
        </tbody>
      </table>

      <p class="fg-gray">HTML</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>&lt;!--:value.sync ()--&gt;
&lt;tabs :list="list" :value.sync="value"&gt;
  &lt;!--slotTitle--&gt;
  &lt;div slot="div1" class="padding15 bg-grayLighter"&gt;div1&lt;/div&gt;
  &lt;div slot="div2" class="padding15 bg-grayLighter"&gt;div2&lt;/div&gt;
&lt;/tabs&gt;
</code></pre>

      <p class="fg-gray">Script</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>export default {
  data() {
    return {
      list: [
        { name: 'div1' },
        { name: 'div2' }
      ],
      value: 'div1'
    }
  }
}
</code></pre>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        list: [
          { name: 'div1' },
          { name: 'div2' }
        ],
        value: 'div1'
      }
    }
  }
</script>



